<template>
    <div class="projectItem">
        <form action="">
            <ul>
                <li class="certain-item">
                    <span class="icon project"></span>
                    <label for="project">项目名称</label>
                    <select name="project" id="project" @change="projectChange">
                        <option :value="item.pId" v-for="(item,index) in projectData">{{item.pName}}</option>
                    </select>
                </li>
                <li class="certain-item">
                    <span class="icon owner"></span>
                    <label for="owner">负责人</label>
                    <select name="owner" id="owner" @change="memberChange">
                        <option :value="item.mId" v-for="(item,index) in members">{{item.mName}}</option>
                    </select>
                </li>
                <li class="certain-item">
                    <span class="icon task"></span>
                    <label for="task">任务名称</label>
                    <select name="task" id="task" @change="taskChange()">
                        <option :value="item.tId" v-for="(item,index) in tasks">{{item.tName}}</option>
                    </select>
                </li>
                <li class="certain-item">
                    <span class="icon progress"></span>
                    <label for="progress">任务进度</label>
                    <progress :value="mProcess" max="100"></progress>
                </li>
                <li class="certain-item">
                    <span class="icon date"></span>
                    <label for="date">截止日期</label>
                    <input type="date" name="date" id="date" :value="mDate"></input>
                </li>
                <li class="certain-item">
                    <span class="icon remark"></span>
                    <label for="remark">备注</label>
                    <input type="text" id="remark" placeholder="请添加备注" :value="notice">
                </li>
                <li class="certain-item" v-for="(item, index) in options">
                    <span :class="{'icon': true, 'checkItem': item.oCheck, 'noCheckItem': !item.oCheck}" @click="toggleCheck(item.oId)"></span>
                    <label for="checkItem">检查项</label>
                    <input type="text" :id="item.oId" placeholder="请添加检查项" :value="item.oName">
                </li>
                <li class="certain-item">
                    <span class="icon addItem"></span>
                    <input type="button" id="addItem" value="添加检查项">
                </li>
            </ul>
        </form>
        <div class="timeLineWrap">
            <h2>
                <span class="icon importantTime"></span>
                <p class="title">重要时间节点</p>
            </h2>
            <ul class="timeLine">
                <li v-for="(item, index) in timeLine">
                    <hr />
                    <p class="timeLineTime">{{item.deadline}}</p>
                    <span class="circle full"></span>
                    <p class="timeLineTitle">{{item.lineName}}</p>
                </li>
            </ul>
            <ProjectMember ref="pMember"></ProjectMember>
        </div>
    </div>
</template>
<script>
import ProjectMember from './ProjectMember'
import {mapState} from 'vuex'
export default {
    components: {
        ProjectMember
    },
    data(){
        return {
            pFlag: false,
            projects: 0,
            mId: 0,
            tId: 0,
            members: [],
            tasks: [],
            mProcess: 0,
            mDate: '',
            notice: '',
            options: [],
            timeLine: []
        }
    },
    computed: {
        ...mapState({
            projectData: 'projectData',
            defaultId: 'pId'
        }),
        pId: {
            get(){
                return this.pFlag ? this.projects : this.defaultId;
            },
            set(v){
                this.projects = v;
            }
        }
    },
    watch: {
        'pId'(){
            let tmp = this.projectData.filter(item => {
                return item.pId == this.pId;
            })[0];
            this.timeLine = tmp.timeLine;
            this.$refs.pMember.getPId(this.pId);
        },
        'mId'(){
            // console.log('***---', this.members);
            // console.log('***---', this.mId);
            let tmp = this.members.filter(item => {
                return item.mId == this.mId;
            })[0];
            this.mProcess = tmp.mProcess;
            this.mDate = tmp.mDate;
            // console.log('this.mProcess', this.mProcess);
            // console.log('this.mDate', this.mDate);
        },
        'tId'(){
            // console.log('---***', this.tasks);
            // console.log('---***', this.tId);
            let tmp = this.tasks.filter(item => {
                return item.tId == this.tId;
            })[0];
            this.notice = tmp.notice;
            this.options = tmp.options;
            // console.log('this.notice', this.notice);
            // console.log('this.options', this.options);
        }
    },
    methods: {
        projectChange(){
            this.pFlag = true;
            let mFlag = false;
            // this.pId = this.$route.query.pId;
            let pOpt = document.getElementById('project').value;
            if(pOpt){
                this.pId = pOpt;
            }
            console.log('******', pOpt);
            console.log('******pId', this.pId);
            this.members = this.projectData.filter(item => {
                return item.pId == this.pId;
            })[0].members;
            let mOpt = document.getElementById('owner');
            for(let i=0;i<mOpt.options.length;i++){
                if(mOpt.options[i].value == this.$route.query.mId){
                    mFlag = true;
                    mOpt.options[i].selected = true;
                }
            }
            if(!mFlag){
                this.mId = this.members[0].mId;
            }
            console.log('***projectChange');
        },
        memberChange(){
            let tFlag = false;
            let mOpt = document.getElementById('owner').value;
            if(mOpt){
                this.mId = mOpt;
            }
            console.log('******', mOpt);
            console.log('******mId', this.mId);
            this.tasks = this.members.filter((item,index) => {
                return item.mId == this.mId;
            })[0].tasks;
            let tOpt = document.getElementById('task');
            for(let i=0;i<tOpt.options.length;i++){
                if(tOpt.options[i].value == this.$route.query.tId){
                    tFlag = true;
                    tOpt.options[i].selected = true;
                }
            }
            if(!tFlag){
                this.tId = this.tasks[0].tId;
            }
            console.log('***memberChange');
        },
        taskChange(){
            let tOpt = document.getElementById('task').value;
            if(tOpt){
                this.tId = tOpt;
            }
            console.log('******', tOpt);
            console.log('******tId', this.tId);
            console.log('***taskChange');
        },
        toggleCheck(oId){
            let tmp = this.options.filter(item => {
                return item.oId == oId;
            })[0];
            tmp.oCheck = !tmp.oCheck;
        }
    },
    beforeCreate() {
        console.log('ProjectItem.vue beforeCreate');
    },
    created() {
        console.log('ProjectItem.vue Created');
    },
    beforeMount() {
        console.log('ProjectItem.vue beforeMount');
    },
    mounted() {
        console.log('ProjectItem.vue mounted');
        console.log(this.$route);
    },
    beforeUpdate() {
        console.log('ProjectItem.vue beforeUpdate');
    },
    updated() {
        console.log('ProjectItem.vue updated');
        this.projectChange();
        this.memberChange();
    },
    beforeDestroy() {
        console.log('ProjectItem.vue beforeDestroy');
    },
    destroyed() {
        console.log('ProjectItem.vue destroyed');
    },
}
</script>
<style scoped>
