    display: inline-block;
    height: 30px;
    line-height: 30px;
    font-size: 14px;
    vertical-align: middle;
    margin-left: 8px;
}
select, input{
    max-width: 60%;
}
h2{
    font-size: 0;
    height: 30px;
    line-height: 30px;
    vertical-align: middle;
}
.importantTime{
    background-position: -120px -48px;
}
.timeLine{
    display: flex;
    justify-content: space-around;
    align-items: center;
    align-content: center;
    font-size: 0;
}
.timeLine li{
    position: relative;
    text-align: center;
    flex: 1;
}
.timeLine li p{
    display: block;
    margin: 0;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.timeLine li:nth-of-type(1) hr{
    width: 50%;
    right: 0;
}
.timeLine li:nth-last-of-type(1) hr{
    width: 50%;
    left: 0;
}
hr{
    width: 100%;
    position: absolute;
    top: 29px;
    z-index: 0;
    margin: 0;
}
.timeLineTime{
    font-size: 12px;
    height: 20px;
    line-height: 20px;
}
.timeLineTitle{
    font-size: 14px;
    height: 20px;
    line-height: 20px;
}
.circle{
    display: inline-block;
    width: 0;
    height: 0;
    margin: 0 auto;
    border: 10px solid #4C6FB0;
    border-radius: 50%;
    z-index: 1;
}
.full{
    border-color: #FF9800;
}
.part1{
    border-color: #FF9800 #4C6FB0 #4C6FB0;
}
.part2{
    border-color: #FF9800 #FF9800 #4C6FB0 #4C6FB0;
}
.part3{
